import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	
	data() {
		return {
			info: {},
			isFinish: false,
			checked1:false,
			checked2:true
		}
	},
	onShow() {
		uni.showLoading({
		    title: '加载中'
		});
		this.getData()
	},
	methods: {
		// 推出俱乐部
		exitClub(){
			if(uni.getStorageSync('isJoin')=='1'){
				uni.navigateTo({
					url:'../../pagesTwo/ExitClubList/ExitClubList'
				})
			}else{
				uni.showToast({
				    title: '您还没有加入俱乐部',
				    mask: "true",
					icon:'none'
				});
			}
			
		},
		// 退出
		exit () {
			Dialog.confirm({
			  title: '提示',
			  message: '确定要退出登录吗？'
			}).then(() => {
			  uni.clearStorageSync()
			  uni.reLaunch({
			  	url: '../../pages/login/login'
			  })
			}).catch(() => {
			  // on cancel
			});
		     
		    },
		// 开关
		onChange1(e) {
			this.checked1 = e.detail
		     this.$api.ShowPhone({
		            token: uni.getStorageSync("token"),
		            user_id: uni.getStorageSync("userId")
			  }).then(res => {
				if (res.data.code === '200') {
				  this.getData()
				}
			  })
		  },
		onChange2(e) {
			this.checked2 = e.detail 
			this.$api.ShowWechat({
			       token: uni.getStorageSync("token"),
			       user_id: uni.getStorageSync("userId")
			 }).then(res => {
				if (res.data.code === '200') {
				  this.getData()
				}
			 })
		  },
		// 获取个人设置信息
		getData () {
		      this.$api.MyselfSetDefaultMobileWechat({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId")
		      }).then(res => {
				  
		        this.info = res.data.data
				// console.log(typeof res.data.data.is_show_mobile)
				if(res.data.data.is_show_mobile===0){
					this.checked1 = false
				}else if(res.data.data.is_show_mobile===1){
					this.checked1 = true
				}
				
				if(res.data.data.is_show_wechat===0){
					this.checked2 = false
				}else if(res.data.data.is_show_wechat===1){
					this.checked2 = true
				}
		        this.isFinish = true
				uni.hideLoading();
		      })
		 },
	}
}
